import React from "react";
import "./App.css";

import Navbar from "./components/Navbar/Navbar";
import Form from "./components/Form/Form";
import PostGrid from './components/PostGrid/PostGrid';
import axios from 'axios'
import { ThemeProvider as MuiThemeProvider } from '@material-ui/core/styles'
import Pagination from '@material-ui/lab/Pagination'
import {makeStyles} from '@material-ui/core/styles'
import './App.css'   
import Grid from '@material-ui/core/Grid'
import BottomNavigation from '@material-ui/core/BottomNavigation'
import Footer from './components/Footer/Footer'
const key ="15976988-c8c786d83f79eeca9bef9c200";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      search:'',
      currentPage: 0,
      Items: [],
      totalHits:0
    };
  



    this.hasContent =false
    this.handleClick = this.handleClick.bind(this);
    this.handlePageChange =this.handlePageChange.bind(this)
  }
  handleClick(e) {
    var query =''
    if (e === null){
       query= this.state.search
    }
    else{
       query= (e.target.value).replace(' ', '+');
       this.setState({
         search:query,
         currentPage:0
       })
    }

    if(query!==''){
      this.hasContent=true;
    }


    const page=this.state.currentPage + 1;
    
    const request = `https://pixabay.com/api/?key=${key}&q=${query}&image_type=photo&page=${page}&per_page=12`
    console.log(request)
    axios
      .get(request)
      .then((res) => {
        this.setState({
          Items:res.data.hits,
          totalHits:res.data.totalHits
        })
       
       
      });
  }
  componentDidMount(){
    
  }
  componentDidUpdate(){

  }
  
  handlePageChange(e,value){
    this.setState({
      currentPage:value
    })
    this.handleClick(e=null)
  }
  
  render() {
    const style={display:''}

    if (!this.hasContent){
      style.display="none"
    }
    else{
      style.display="block"
    }
    console.log()

    return (

        <MuiThemeProvider >

        <div>
            <Navbar title="ImageHunt" />
            <Form onTextChange={this.handleClick} />
            <PostGrid posts={this.state.Items} currentPage={this.state.currentPage} query={this.state.search} />
            <BottomNavigation  >
               <Grid container className="grid-content" > 
                <Grid item lg={5}></Grid>
                <Grid style={style}  item lg={3} spacing={5}  > <Pagination   page={this.state.currentPage}  color="primary" count={4} onChange={this.handlePageChange} /></Grid>
                <Grid item lg={4}></Grid>
            </Grid>
            </BottomNavigation>
           
          
          <Footer />
           
        </div>
        
        </MuiThemeProvider>
        
      
    );
  }
}

export default App;
