import React from "react";
import FlatButton from 'material-ui/FlatButton'
import IconButton from 'material-ui/IconButton';
import ZoomIn from 'material-ui/svg-icons/action/zoom-in'
import { makeStyles } from "@material-ui/core/styles";
import Modal from "../../../Modal/Modal";
import GridTile from 'material-ui/GridList/GridTile'
function PostGridItem(props) {
    const [open, setOpen] = React.useState(false);
  const useStyles = makeStyles({
    gridlistTitle: {

    
    },
    img:{
    
    }
  });
  function showModal() {
        setOpen(true)
  }
  function closeModel(){
      setOpen(false)
  }
  const classes = useStyles();
  return (
   
    
        <GridTile className={classes.gridlistTitle}
        title={props.src.tags}
        subtitle={
          <span>
            by <strong>{props.src.user}</strong>
          </span>
        }
        actionIcon={
          <IconButton>
            <ZoomIn color="white"  onClick={showModal} ></ZoomIn>
          </IconButton>
        }
        
        >
        <img className={classes.img}
          src={props.src.largeImageURL}
        width="100%"
        height="100%"
          onClick={showModal}
        /> 
        <Modal isOpen={open} onClose={closeModel} img={props.src.largeImageURL} />
        </GridTile>
       
    
    
  );
}

export default PostGridItem;
