import React from "react";
import PostGridItem from "./assets/PostGridItem/PostGridItem";
import { makeStyles } from "@material-ui/core/styles";

import GridList from 'material-ui/GridList'


function PostGrid(props) {
  const useStyles = makeStyles((theme) => ({
    // root: {
    //   display: "flex",
    //   flexWrap: "wrap",
    //   justifyContent: "space-around",
    //   overflow: "hidden",
    //   backgroundColor: theme.palette.background.paper,
    // },
    // gridList: {
    //   // width: "100%",
    //   // height:"100%"
      
    // },
  }));
  const classes = useStyles();
  const style = { textAlign: "center" };
  const item =
    props.posts.length !== 0 || props.query ===''  ? (
      props.posts.map((post) => (
        <PostGridItem src={post} key={post.id} />
      ))
    ) : (
      <h1 style={style}>No Result Found</h1>
    );

  return (
    
      <GridList className={classes.gridList} cellHeight={150} cols={3}>
        {item}
      </GridList>
    
  );
}
export default PostGrid;
