import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField' ;
import Grid from '@material-ui/core/Grid';     
import Tooltip from '@material-ui/core/Tooltip'                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          
export default function ResponsiveDialog(props) {
  
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const [open, setOpen] = React.useState(false);
  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };
    function onCopy(e){
        const copyText=document.getElementById("txt")
          /* Select the text field */
  copyText.select();
  copyText.setSelectionRange(0, 99999); /*For mobile devices*/

  /* Copy the text inside the text field */
  document.execCommand("copy");
handleOpen();
    }

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={props.isOpen}
        onClose={props.onClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title" style={{textAlign:"center"}} >Copy Image</DialogTitle>
        <DialogContent>
          <img src={props.img} width="100%"/>
        </DialogContent>
        <DialogActions>
            <Grid container >
            
            <Grid item sm={6} style={{marginLeft:".8em"}} lg={10} ><TextField id="txt" fullWidth value={props.img} disabled /></Grid>
            <Grid item lg={1}>
            <Tooltip open={open}   title="Copied">
                <Button style={{marginBottom:".9em",marginLeft:".5em"}} onClick={onCopy} >Copy</Button>
            </Tooltip>
            </Grid>
            </Grid>

{/*             
          <Button onClick={props.onClose} color="primary" autoFocus>
            Agree
          </Button> */}
        </DialogActions>
      </Dialog>
    </div>
  );
}
